import * as types from "../action-types"

let initState = {
    user: {
        username: null,
        password: null,
        cartList: []
    },// user
    success: null,//
    error: null,//
};

export default function (state = initState, action) {
    switch (action.type) {
        case types.SET_SESSION_INFO:
            //console.log(action.session)
            return action.session;
        default:
            return state;
    }
}





