import React, {Component} from "react";
import "./index.less";
import avatar from "../../common/image/profiles.png";
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import actions from "../../store/actions/session";
import BNavBar from "../../components/BNavbar/index";

@connect(
    state => state.session,
    actions
)
export default class User extends Component {
    componentWillMount() {
        console.log(this.props);
    }

    render() {
        return (
            <div className="profile">
                <BNavBar/>
                <img src={avatar}/>
                <div className="my-user">
                    <span>{this.props.user.username}</span>
                    <a href=""></a>
                </div>

                <span className="my-order"></span>
                <ul className="order">
                    <li><i className="iconfont icon-qianbao"><span></span></i></li>
                    <li><i className="iconfont icon-daifahuo-copy"><span></span></i></li>
                    <li><i className="iconfont icon-yifahuo"><span></span></i></li>
                    <li><i className="iconfont icon-daipingjia"><span></span></i></li>
                    <li><i className="iconfont icon-tuihuanhuo"><span></span></i></li>
                </ul>

                <span className="my-server"></span>
                <ul className="server server-one">
                    <li><i className="iconfont icon-pintuan"><span></span></i></li>
                    <li><i className="iconfont icon-zhongchou1"><span></span></i></li>
                    <li><i className="iconfont icon-yaoqing"><span></span></i></li>
                    <li><i className="iconfont icon-youhuiquan"><span></span></i></li>
                </ul>
                <ul className="server server-two">
                    <li><i className="iconfont icon-hongbao1"><span></span></i></li>
                    <li><i className="iconfont icon-gouwudai"><span></span></i></li>
                    <li><i className="iconfont icon-lipinqia"><span></span></i></li>
                    <li><i className="iconfont icon-huiyuan1"><span></span></i></li>
                </ul>
                <ul className="server server-three">
                    <li><i className="iconfont icon-zuanshi1"><span></span></i></li>
                    <li><i className="iconfont icon-zuji1"><span></span></i></li>
                    <li><i className="iconfont icon-shoucang1"><span></span></i></li>
                    <li><i className="iconfont icon-dizhi"><span></span></i></li>
                </ul>
                <ul className="server server-four">
                    <li><i className="iconfont icon-kefu"><span></span></i></li>
                    <li><i className="iconfont icon-bangzhu"><span></span></i></li>
                    <li><i className="iconfont icon-shezhi"><span></span></i></li>
                    <li><i className="iconfont icon-gengduo"><span></span></i></li>
                </ul>

                <div className="logout" onClick={this.props.logout}></div>

            </div>
        )
    }
}





